.ui-header {
  max-width: 1280px;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  transition: "top 0.5s ease-in-out";
  z-index: 100;

  a {
    color: #3f89e6;
    text-decoration: none;
  }
}

// size
.ui-header-md {
  padding: 36px 0;
}
.ui-header-lg {
  padding: 40px 0;
}

.items {
  display: flex;
  .item {
    transition: 0.5s;
    &:hover {
      scale: 1.5;
    }
    & + .item {
      margin-left: 28px;
    }
  }
}
